@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@100;300;400;700&display=swap);
.ProfileContainer {
  background: #191622;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentWrapper {
  width: 80vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.AboutMe {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.AboutMe * {
  color: #ffffff;
}

.AboutMe button {
  width: 250px;
  height: 70px;

  border: solid 2px #67e480;
  background: none;
  font: 700 1.2rem Heebo;
  color: #67e480;

  transition: 0.8s;
}

.AboutMe button:hover {
  background: #67e480;
  color: #191622;
  cursor: pointer;
}

.AboutMe h2 {
  margin-bottom: 30px;
  font: 700 2rem "Heebo";
  letter-spacing: 3px;
}

.AboutMe p {
  margin-bottom: 30px;
  font: 500 1.1rem "Heebo";
}

.AboutMe span {
  color: #67e480;
}
.Photo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 100%;
}

.Photo {
  width: 400px;
  height: 400px;

  border-radius: 50%;
  background-image: url("https://avatars.githubusercontent.com/u/67125215?s=400&u=554b73aeced7fd3787d632c6b94a083fa34eeb1b&v=4");
}

.Content {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  color: #e7e7e7;

  height: 100%;
}

.SeparatorContainer {
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font: 700 1.4rem "Heebo";
  color: #191622;
}

.TechsContainer {
  background: #191622;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

.TechsSubHeader {
  margin-top: 50px;
  margin-bottom: 30px;
  font-family: Heebo;
  color: rgb(255, 255, 255);
}

.TechsContainer .TechsSubHeader {
  margin-top: 50px;
  margin-bottom: 30px;
}
.TechsContentContainer {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.TechsContent {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  height: 100%;
}

.TechsContent span {
  color: #67e480;
  font: 700 1.8rem "Heebo";
}

.TechsContent div {
  width: 60vw;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font: 300 6rem "Heebo";
}

.ContentContainer {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  color: #e7e7e7;

  height: 100%;
  border-bottom: 2px solid #67e480;
}

html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #191622;
}

::-webkit-scrollbar-thumb {
  background: #483c67;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #483c67;
}

.toTop {
  width: 70px;
  height: 70px;
  border: 4px solid #67e480;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 100px;
  margin-bottom: 100px;
  cursor: pointer;

  transition: 0.8s;
}

.toTop:hover {
  filter: brightness(90%);
}
.NavbarContainer {
  background: #191622;

  height: 70px;
  display: flex;
  justify-content: center;
}

a {
  text-decoration: none;

  transition: 0.3s;
}

.LinksContainer {
  width: 30%;
  height: 100%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;

  font: 700 1.1rem "Heebo";
}

.LinksContainer a {
  color: #988bc7;
}

.LinksContainer a:hover {
  filter: brightness(70%);
}

.NavbarContainer a:first-child {
  align-self: center;
  margin-left: 30px;
  font-family: "Heebo", sans-serif;
}

#contact {
  color: #67e480;
}

.AboutContentContainer {
  width: 340px;
  height: 90px;

  display: flex;
  justify-content: space-between;
  flex-direction: column;

  font-family: "Heebo";
}

.AboutContentContainer span {
  font-size: 0.8rem;
}

.AboutContentContainer strong {
  color: #67e480;
}

.AboutMeContainer {
  background: #191622;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  font-family: "Heebo";
}

.AboutMeSubHeader {
  margin-top: 50px;
  margin-bottom: 30px;
  color: #ffffff;
}

.AboutMeSubHeader span {
  color: #67e480;
}
.AboutMeHeader {
  border: 3px solid #67e480;

  width: 320px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #67e480;
  font: 500 2rem "Heebo";
}

.AboutMeItemsContent {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #ffffff;
}

.AboutMeItems {
  width: 40vw;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  border-top: 2px solid #67e480;
}

.FooterContainer {
  background: #0d0c13;
  height: 380px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  border-top: 3px solid #67e480;
}

.ContactContainer {
  color: white;
  width: 80%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  font-family: "Heebo";
}

.BottomFooterContainer {
  height: 50px;
  width: 100%;

  color: #191622;

  letter-spacing: 3px;
  font: 700 0.9rem "Heebo";

  display: flex;
  align-items: center;
  justify-content: center;
  background: #67e480;
}

.Who-I-Am {
  display: grid;
  align-self: center;
  grid-template-rows: 1fr 2fr;
  width: 85%;
  height: 100%;
}

.Who-I-Am strong {
  align-self: center;
}

.SocialMediaContainer {
  display: grid;
  grid-template-rows: 1fr 2fr;
}
.SocialMediaContainer strong {
  align-self: center;
}
.SocialIcon {
  height: 40px;
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.SocialIcon span {
  width: 70%;
  align-self: flex-start;
}
.SocialIcon:hover {
  color: #67e480;
}

.Icon:hover {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

#instagram {
  transition: 0.2s;
}
#instagram:hover {
  background: rgb(161, 34, 195);
  background: linear-gradient(
    214deg,
    rgb(186, 30, 230) 0%,
    rgba(181, 34, 168, 1) 39%,
    rgba(195, 34, 151, 1) 50%,
    rgb(255, 192, 56) 100%
  );
  transform: translateY(-3px);
  transform: translateY(-3px);
  /* box-shadow: 0 4px 17px rgba(197, 197, 197, 0.521); */
  color: #ffffff;

  color: #ffffff;
  padding: 2px;
  border-radius: 50%;
}

#github {
  color: white;
  transition: 0.2s;
}
#github:hover {
  background: white;
  color: black;
  padding: 2px;
  border-radius: 50%;
}
#linkedin {
  transition: 0.2s;
}
#linkedin:hover {
  background: #2867b2;

  padding: 2px;
  border-radius: 50%;
}
.ContactFieldContainer {
  display: grid;
  grid-template-rows: 1fr 2fr;
}
.ContactFieldContainer strong {
  align-self: center;
}

.GalleryContainer {
  background: rgb(13, 12, 19);
  background: linear-gradient(
    360deg,
    rgba(13, 12, 19, 1) 0%,
    rgba(13, 12, 19, 1) 16%,
    rgba(25, 22, 34, 1) 100%
  );
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  font-family: "Heebo";
  color: #67e480;
}
.GalleryContainer h2 {
  color: #67e480;
}

#addImageIcon {
  margin-top: 22px;
  transition: 0.2s;
  cursor: pointer;
}

#addImageIcon:hover {
  color: #67e480;
}

.GalleryModalContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #0d0c13;
  width: 50%;
  height: 60%;
  border-radius: 2px;
}

.CloseButton {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #67e480;
  cursor: pointer;
  transition: 0.2s;
}

.CloseButton:hover {
  filter: brightness(70%);
}

.Preview {
  width: 70%;
  height: 100%;
  background-size: cover;
}

.GalleryModalContiner form {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

.GalleryModalContainer form {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.GalleryModalContainer form > * {
  width: 80%;
  height: 30px;
  outline: none;
  margin-top: 20px;
  margin-bottom: 20px;
}

form > input[type="text"] {
  color: white;
  background: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
  padding: 10px;

  border-bottom: 2px solid white;
  transition: 0.3s;
}

form input[type="text"]:hover {
  color: #67e480;

  border-bottom: 2px solid #67e480;
}

form input[type="text"]:focus {
  color: #67e480;

  border-bottom: 2px solid #67e480;
}

form > input[type="submit"] {
  border: 2px solid #67e480;
  border-radius: 2px;
  background: transparent;
  color: #67e480;
  height: 60px;
  font: 700 1rem "Heebo";
  transition: 0.3s;
}

form > input[type="submit"]:hover {
  background: #67e480;
  color: #0d0c13;
  font: 700 1rem "Heebo";
}

